___CSS_LOADER_EXPORT___.push([module.id, `.mess-forum {
  display: flex;
  border: 1px solid #DCE3E8;
  border-top: 0;
  background: #FFF;
}
.mess-forum__user-info, .mess-forum__head {
  display: flex;
  align-items: center;
}
.mess-forum__user-info {
  flex-direction: column;
  padding: 22px 20px;
  border-right: 1px solid #DCE3E8;
  flex: 0 0 210px;
}
.mess-forum__image-wrap {
  width: 100px;
  height: 100px;
  border-radius: 100%;
  overflow: hidden;
  margin: 0 0 17px;
}
.mess-forum__image {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  height: 100%;
}
.mess-forum__name {
  font-size: 13px;
  font-weight: 600;
  line-height: 1.1;
  text-align: center;
  margin: 0 0 15px;
}
.mess-forum__amount-mess {
  margin: 0 0 4px;
}
.mess-forum__amount-mess, .mess-forum__date-reg {
  color: #8B919E;
  font-size: 12px;
  font-weight: 500;
}
.mess-forum__amount-mess-value {
  color: #000;
  font-weight: 600;
}
.mess-forum__amount-mess-title, .mess-forum__date-reg-title {
  margin: 0 5px 0 0;
}
.mess-forum__body {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.mess-forum__head, .mess-forum__text-wrap {
  border-bottom: 1px solid #DCE3E8;
}
.mess-forum__head {
  background: #F2F5FA;
  padding: 12px 15px;
  justify-content: space-between;
}
.mess-forum__head-date, .mess-forum__head-num {
  color: #8B919E;
  font-size: 12px;
  font-weight: 500;
}
.mess-forum__head-num {
  color: #454B58;
}
.mess-forum__text-wrap {
  padding: 26px 25px 28px;
  flex: 1 1 auto;
}
.mess-forum__text {
  color: #454B58;
  line-height: 1.8;
}
.mess-forum__footer {
  padding: 24px 25px 22px;
}
.mess-forum__footer-link {
  color: #F04973;
  font-weight: 600;
  text-decoration: underline;
  margin: 0 30px 0 0;
}
.mess-forum__footer-link_quote {
  color: #8B919E;
